import Swal from "sweetalert2"
import { fetchWithToken } from "../../helpers/fetch"
import types from "../types"

export const startGetSelling = () => {
    return async dispatch => {
        try {
            const response = await fetchWithToken('books/user/selling', null)
            const { books } = await response.json()
            dispatch(setSelling(books))
        } catch (error) {
            Swal.fire('Error', 'Ocurrió un error', 'error')
        }
    }
}

const setSelling = books => ({
    type: types.SELLING_GET,
    payload: books
})

export const startDeleteBook = id => {
    return async dispatch => {
        try {
            const response = await fetchWithToken(`books/${id}`, { id }, 'DELETE')
            const body = await response.json()

            if(!body.ok) {
                return Swal.fire('Error', body.msg, 'error')
            }
            dispatch(deleteBook(id))
            Swal.fire('Éxito', 'Libro borrado correctamente', 'success')
        } catch (error) {
            Swal.fire('Error', 'Ocurrió un error', 'error')
        }
    }
}

const deleteBook = id => ({
    type: types.SELLING_DELETE,
    payload: id
})