import React from 'react'
import { Link } from 'react-router-dom'
import categorySelectOptions from '../../helpers/categorySelectOptions'

const CategoriesScreen = () => {
    return (
        <main className="categories__categories">
            <section>
                <h2>Categorías</h2>
                <div className="categories__mapped">
                    {
                        categorySelectOptions.map(({label, icon, value}) => (
                            <Link to={`/categorias/${value}`}>
                                <h2>{ label }</h2>
                                { icon }
                            </Link>
                        ))
                    }
                </div>
            </section>
        </main>
    )
}

export default CategoriesScreen
