import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { BrowserRouter as Router, Switch, Redirect, Route} from 'react-router-dom'
import Footer from "../components/Footer";
import NavBar from '../components/NavBar'
import LoginScreen from "../containers/auth/LoginScreen";
import RegisterScreen from "../containers/auth/RegisterScreen";
import BookScreen from "../containers/books/BookScreen";
import EditScreen from "../containers/books/EditScreen";
import SellScreen from "../containers/books/SellScreen";
import Cart from "../containers/cart/Cart";
import CategoriesScreen from "../containers/categories/CategoriesScreen";
import CategoryScreen from "../containers/categories/CategoryScreen";
import ErrorScreen from "../containers/ErrorScreen";
import HomeScreen from '../containers/HomeScreen'
import SearchScreen from "../containers/SearchScreen";
import ProfileScreen from "../containers/user/ProfileScreen";
import { startChecking } from "../store/actions/auth";
import PrivateRoute from "./PrivateRoute";
import PublicRoute from "./PublicRoute";

const MainRouter = () => {

    const dispatch = useDispatch()
    const { checking, uid } = useSelector(state => state.auth)

    useEffect(() => {
        dispatch(startChecking())
    }, [dispatch])

    if(checking) {
        return <h1>Cargando</h1>
    }

    return (
        <Router>
            <div>
                <NavBar/>
                <Switch>
                    <Route exact path="/" component={ HomeScreen }/>
                    <Route exact path="/libro/:id" component={ BookScreen }/>
                    <Route exact path="/categorias" component={ CategoriesScreen }/>
                    <Route exact path="/categorias/:category" component={ CategoryScreen }/>
                    <Route exact path="/buscar/:term" component={ SearchScreen }/>
                    <PublicRoute exact path="/login" component={ LoginScreen } isAuthenticated={ !!uid }/>
                    <PublicRoute exact path="/registro" component={ RegisterScreen } isAuthenticated={ !!uid }/>
                    <PrivateRoute exact path="/vender" component={ SellScreen } isAuthenticated={ !!uid }/>
                    <PrivateRoute exact path="/editar/:id" component={ EditScreen } isAuthenticated={ !!uid }/>
                    <PrivateRoute exact path="/carrito" component={ Cart } isAuthenticated={ !!uid }/>
                    <PrivateRoute exact path="/perfil" component={ ProfileScreen } isAuthenticated={ !!uid }/>
                    <Route exact path="/error" component={ ErrorScreen } />
                    <Redirect to="/error"/>
                </Switch>
                <Footer/>
            </div>
        </Router>
    )
}

export default MainRouter