import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useParams, useHistory } from 'react-router-dom'
import { baseUrl, fetchWithToken } from '../../helpers/fetch'
import { startAddToCart } from '../../store/actions/cart'

const BookScreen = () => {

    const { id } = useParams()
    const history = useHistory()
    const [book, setBook] = useState(null)
    const dispatch = useDispatch()
    const { uid } = useSelector(state => state.auth)

    useEffect(() => {
        fetch(`${ baseUrl }/books/${ id }`)
        .then(response => response.json())
        .then(body => {
            if(body.ok) {
                setBook(body.book)
            } else {
                history.push('/error')
            }
        })
        .catch(_ => history.push('/error'))
    }, [ id, history ])

    const handleAddToCart = () => {
        dispatch(startAddToCart(id))
    }

    const handleEdit = () => {
        history.push(`/editar/${ id }`)
    }

    const handleDeleteBook = async () => {
        await fetchWithToken(`books/${ id }`, { id }, 'DELETE')
        history.push('/')        
    }

    return (
        <main className="book">
            {
                book &&
                <section>
                    <div className="book__details">
                    <h1>{ book.title }</h1>
                    <div className="description">
                        <h3>Autor: { book.author }</h3>
                        <h3>Editorial: { book.publisher }</h3>
                        <h3>Vendedor:</h3>
                        <h4>{ book.seller.firstName }, { book.seller.province }</h4>
                    </div>
                    <img src={ `${ book.imageUrl }` } alt={ book.name } className="book__mobile-img" />
                    <div className="book__price">
                        <h4>$ { book.price }</h4>
                    </div>
                    {
                        book.seller._id === uid
                        ?   <div className="book__buttons">
                                <button onClick={ handleEdit }>Editar Libro</button>
                                <button onClick={ handleDeleteBook }>Borrar Libro</button>
                            </div>
                        :   <div className="book__buttons">
                                <button onClick={ handleAddToCart }>Agregar Al Carrito</button>
                                <button>Comprar</button>
                            </div>   
                    }
                    </div>
                    <div className="book__hero">
                    <div className="book__trian"></div>
                    <div className="book__product-img">
                        <img src={ `${ book.imageUrl }` } alt={ book.name } />
                    </div>
                    </div>
                </section>
            }
        </main>
    )
}

export default BookScreen
