import React, { useEffect, useState } from 'react'
import { FaShoppingCart } from "react-icons/fa";
import { useDispatch, useSelector } from 'react-redux';
import CartItem from '../../components/CartItem'
import { startGetCart } from '../../store/actions/cart';

const Cart = () => {

    const [total, setTotal] = useState(null)
    const dispatch = useDispatch()
    const { cart } = useSelector(state => state.cart)

    useEffect(() => {
        dispatch(startGetCart())
    }, [ dispatch ])

    useEffect(() => {
        if(cart) {
            setTotal(cart.reduce((sum, cartItem) => sum + cartItem.price, 0))
        }
    }, [ cart ])

    return (
        <div className="cart__container">
            {
                cart.length 
                ?    <section className="cart">
                        <h3 className="cart__title"><span><FaShoppingCart /></span>Carrito</h3>
                        <div className="cart__productos-container">
                            {
                                cart.map(cartItem => <CartItem key={cartItem.id} { ...cartItem }/>)
                            }
                        </div>
                        <div className="cart__total-precio">
                            <div className="cart__total">Total</div>
                            <div className="cart__precio-final">$ { total }</div>
                        </div>
                        <div className="cart__comprar">
                            <button className="cart__seguir-btn">Seguir comprando</button>
                            <button className="cart__pagar-btn">Pagar</button>
                        </div>
                    </section>
                :   <section className="cart">
                        <h3 className="cart__title"><span><FaShoppingCart /></span>Carrito</h3>
                            <div className="cart__productos-container">
                                <h3>Carrito Vacío</h3>
                            </div>
                    </section>
            }
        </div>
    )
}

export default Cart
