import React, { useState } from 'react'
import { Link, useHistory } from 'react-router-dom'
import { FaBars, FaSearch, FaShoppingCart } from "react-icons/fa";
import { useDispatch, useSelector } from 'react-redux';
import { startLogout } from '../store/actions/auth';
import logo from '../assets/a.png'
import categorySelectOptions from '../helpers/categorySelectOptions';
import useForm from '../hooks/useForm';

const NavBar = () => {

    const [categories, setCategories] = useState(false)
    const history = useHistory()
    const { uid, name } = useSelector(state => state.auth)
    const dispatch = useDispatch()

    const [ form, handleInputChange ] = useForm({
        search: ''
    })

    const { search } = form

    const handleLogout = () => {
        dispatch(startLogout())
    }

    const handleCategories = () => {
        setCategories(!categories)
    }

    const handleSearch = e => {
        e.preventDefault()
        history.push(`/buscar/${ search }`)
    }

    return (
        <nav>
            <div className="nav__container">
                <Link to="/" className="nav__logo">
                    <img src={logo} alt="Mercado Libros" />
                </Link>
                <p className="nav__desktop" id="categories"onClick={ handleCategories }>Categorías</p>
                <form onSubmit={ handleSearch }>
                    <button type="submit">
                        <FaSearch/>
                    </button>
                    <input type="text" 
                    name="search" 
                    placeholder="Buscá un libro..."
                    value={ search }
                    onChange={ handleInputChange }
                    />
                </form>
                <div className="hamburger nav__mobile">
                    <FaBars fontSize="40"/>
                </div>
                <ul className="nav__links nav__desktop">
                    {
                        uid
                        ?   <>
                                <li>
                                    <Link to="/vender">Vender</Link>
                                </li>
                                <li>
                                    <Link to="/perfil">
                                   
                                        { name }</Link>
                                </li>
                                <li>
                                    <Link to="/carrito"><FaShoppingCart/></Link>
                                </li>
                                <li>
                                    <span onClick={ handleLogout }>Cerrar Sesión</span>
                                </li>
                            </>
                        :   <>
                                <li>
                                    <Link to="/vender">Vender</Link>
                                </li>
                                <li>
                                    <Link to="/login">Iniciar Sesion</Link>
                                </li>
                                <li>
                                    <Link to="/registro">Registrarse</Link>
                                </li>
                            </>
                        
                    }
                    
                </ul>
            </div>
            <div className="nav__categories">
                <ul>
                    {
                        categorySelectOptions.slice(0,13).map((category, i) => (
                            <div key={i}>
                                <li className={ categories ? 'nav__show-categories' : ''}>
                                    {
                                        i !== 12
                                        ? <Link to={ `/categorias/${ category.value }` }>{ category.label }</Link>
                                        : <Link to="/categorias">Ver más</Link>
                                    }
                                </li>            
                            </div>
                        ))
                    }
                </ul>
            </div>
        </nav>
    )
}

export default NavBar
