import React, { useState } from 'react'
import { FaUnlockAlt, FaUserCircle } from "react-icons/fa";
import useForm from '../../hooks/useForm';
import { Link } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { startLogin } from '../../store/actions/auth';

const LoginScreen = () => {

    const dispatch = useDispatch()
    const [remember, setRemember] = useState(false)

    const [ form, handleInputChange ] = useForm({
        email: localStorage.getItem('email') || '',
        password: ''
    })
    const { email, password } = form

    const handleSubmit = e => {
        e.preventDefault()
        if(remember) {
            localStorage.setItem('email', email)
        } else {
            localStorage.removeItem('email')
        }
        dispatch(startLogin(email, password))
    }

    const handleCheckboxChange = () => {
        setRemember(!remember)
    }

    return (
        <main className="login__screen">
            <section>
                <div className="login__signup">
                    <FaUserCircle fontSize="40"/>
                    <h3>¿Todavia no estas registrado?</h3>
                    <Link to="/registro" className="login__registerbtn">Crea tu cuenta</Link>
                </div>

                <div className="login__hero">
                <div className="login__trian"></div>
                <div className="login__signin">
                    <FaUnlockAlt fontSize="40" className="login__icon"/>
                    <form onSubmit={ handleSubmit }>
                        <div className="login__form-inputs">
                            <article>
                                <label htmlFor="email">Email</label>
                                <input
                                    type="email"
                                    name="email"
                                    value={ email }
                                    onChange={ handleInputChange }
                                />
                            </article>
                            <article>
                                <label htmlFor="password">Contraseña</label>
                                <input
                                    type="password"
                                    name="password"
                                    value={ password }
                                    onChange={ handleInputChange }
                                />
                            </article>
                        </div>

                        <div className="sell__cont-terminos">
                            <label className="sell__terminos"
                            >Recordar Usuario
                            <input 
                            type="checkbox"
                            checked={ remember }
                            onChange={ handleCheckboxChange }
                            />
                            <span className="sell__checkmark"></span>
                            </label>
                        </div>
                        <div className="submit">
                            <button type="submit">Ingresar</button>
                        </div>
                    </form>
                </div>
                </div>
            </section>
        </main>
    )
}

export default LoginScreen
