import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import Swal from 'sweetalert2'
import CategoryBook from '../../components/CategoryBook'
import { fetchWithoutToken } from '../../helpers/fetch'

const CategoryScreen = () => {

    const { category } = useParams()

    const [books, setBooks] = useState(null)

    useEffect(() => {
        fetchWithoutToken(`books/category/${ category }`)
        .then(response => response.json())
        .then(body => setBooks(body.books))
        .catch(_ => Swal.fire('Error', 'Ocurrió un error', 'error'))
    }, [ category ])

    return (
        <div className="category__category">
            <section>
                <h2>{ category }</h2>
                <div className="category__books">
                    {
                        books && books.length
                        ? books.map(book => (
                            <CategoryBook key={ book.id } { ...book }/>
                        ))
                        : <h1>Parece que no hay libros en esta categoría</h1>
                    }
                </div>
            </section>
        </div>
    )
}

export default CategoryScreen
