import React from 'react'
import { FaShoppingCart } from 'react-icons/fa'
import { useDispatch } from 'react-redux'
import { Link } from 'react-router-dom'
import { startAddToCart } from '../store/actions/cart'

const CategoryBook = ({ id, title, author, price, imageUrl }) => {

    const dispatch = useDispatch()

    const handleAddToCart = () => {
        dispatch(startAddToCart(id))
    }

    return (
        <div className="category__book">
            <Link to={ `/libro/${ id }` }className="category__libro" >
                <img src={ imageUrl } alt={ title } />
                <div>
                    <h4>{ title }</h4>
                    <p>{ author }</p>
                    <h4>$ { price }</h4>
                </div>
            </Link>
            <FaShoppingCart fontSize="30" className="home__heart" onClick={ handleAddToCart }/>
        </div>
    )
}

export default CategoryBook
