import React from 'react'
import { FaTimes } from "react-icons/fa";
import { useDispatch } from 'react-redux';
import { startDeleteFromCart } from '../store/actions/cart';

const CartItem = ({ id, title,  author, price, imageUrl }) => {
    
    const dispatch = useDispatch()
    const handleRemoveFromCart = () => {
        dispatch(startDeleteFromCart(id))
    }

    return (
        <article className="cart__producto">
            <div className="cart__producto-info">
                <img src={`${imageUrl}`} alt={`${title}`} />
                <div className="cart__desktop">
                    <h4 className="cart__titulo">{ title }</h4>
                    <p className="cart__author">{ author }</p>
                </div>
            </div>
            <p className="cart__precio">$ { price }</p>
            <div className="cart__close">
                <FaTimes fontSize="30" onClick={ handleRemoveFromCart }/>
            </div>
    </article>
    )
}

export default CartItem
