import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Swal from "sweetalert2";
import HomeBook from "../components/HomeBook";
import { baseUrl } from "../helpers/fetch";

const HomeScreen = () => {
    
    const [books, setBooks] = useState(null)

    useEffect(() => {
        fetch(`${ baseUrl }/books`)
        .then(response => response.json())
        .then(body => setBooks(body.books))
        .catch(error => Swal.fire('Error', error.msg, 'error'))
    }, [])

    return (
        <main className="home">
            <div className="home__productos">
                <h2>Explorá</h2>

                {   
                    books && 
                    [ ...(new Set(books.map(book => book.category))) ].map(category => (
                        <section key={ category }>
                            <div className="home__productos-texto-container">
                                <h3 className="productos-texto">{ category }</h3>
                                <Link to={ `/categorias/${ category }` } className="home__ver-mas">Ver más</Link>
                            </div>
                            <div className="home__libros">
                                {
                                    books.filter(book => book.category === category).slice(0,6).map(book => (
                                        <HomeBook key={ book.id } { ...book }/>
                                    ))
                                }
                            </div>
                        </section>
                    ))
                }
            </div>
        </main>
    )
}

export default HomeScreen
