import React from 'react'
import { FaTimes } from 'react-icons/fa'
import { useDispatch } from 'react-redux'
import { Link } from 'react-router-dom'
import { startDeleteBook } from '../store/actions/selling'

const ProfileBook = ({ id, title, author, price, imageUrl }) => {

    const dispatch = useDispatch()

    const handleDeleteProduct = () => {
        dispatch(startDeleteBook(id))
    }

    return (
        <div className="home__book">
            <Link to={ `/libro/${ id }` }className="home__libro" >
                <img src={ imageUrl } alt={ title } />
                <div className="libro-info">
                    <h4>{ title }</h4>
                    <p>{ author }</p>
                    <h4>$ { price }</h4>
                </div>
            </Link>
            <FaTimes fontSize="30" className="home__heart" onClick={ handleDeleteProduct }/>   
        </div>
    )
}

export default ProfileBook
