import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import Swal from 'sweetalert2'
import ProfileBook from '../../components/ProfileBook'
import { fetchWithToken } from '../../helpers/fetch'
import { startGetSelling } from '../../store/actions/selling'

const ProfileScreen = () => {

    const [user, setUser] = useState(null)

    const { books } = useSelector(state => state.selling)
    const dispatch = useDispatch()

    useEffect(() => {
        fetchWithToken('auth/user', null)
        .then(response => response.json())
        .then(({ user }) => setUser(user))
        .catch(_ => Swal.fire('Error', 'Ocurrió un error', 'error'))
    }, [])

    useEffect(() => {
        dispatch(startGetSelling())
    }, [dispatch])

    return (
        <main className="profile">
                {
                    user &&
                        <section className="profile__container">
                            <h2>Perfil</h2>
                            <div className="profile__info">
                                <img src={ `${ user.imgUrl }` } alt={ user.firstName } />
                                <h2>{ user.firstName } { user.lastName }</h2>
                            </div>
                            <section className="profile__products">
                                <h2>Tus Publicaciones</h2>
                                <div className="profile__products-books">
                                    {
                                        books &&
                                        books.map(book => (
                                            <ProfileBook key={ book.id } { ...book } />
                                        ))
                                    }
                                </div>
                            </section>
                        </section>
                }
        </main>
    )
}

export default ProfileScreen
