import React, { useEffect, useState } from 'react'
import { useHistory, useParams } from 'react-router-dom'
import booksImg from '../../assets/books.png'
import { FaInfoCircle, FaTag } from "react-icons/fa";
import useForm from '../../hooks/useForm';
import Select from 'react-select';
import categorySelectOptions from '../../helpers/categorySelectOptions';
import uploadFile from '../../helpers/uploadFile';
import { fetchWithoutToken, fetchWithToken } from '../../helpers/fetch';
import Swal from 'sweetalert2';
import { useSelector } from 'react-redux';

const EditScreen = () => {

    const [category, setCategory] = useState(null)
    const [imageUrl, setImageUrl] = useState(null)
    const [file, setFile] = useState(null)
    const [isUsed, setIsUsed] = useState(false)
    const [submitting, setSubmitting] = useState(false)

    const history = useHistory()
    
    const { id } = useParams()
    const { uid } = useSelector(state => state.auth)
    
    const [ form, handleInputChange, setValues ] = useForm({
        title: '',
        author: '',
        publisher: '',
        price: ''
    })

    const { title, author, publisher, price } = form

    useEffect(() => {
        fetchWithoutToken(`books/${ id }`)
        .then(response => response.json())
        .then(body => {
            if(!body.ok || body.book.seller._id !== uid) {
                return history.push('/error')
            }
            const { book } = body
            setValues({
                title: book.title,
                author: book.author,
                publisher: book.publisher,
                price: book.price
            })
            setCategory(book.category)
            setImageUrl(book.imageUrl)
            setIsUsed(book.isUsed)
        })
        .catch(_ => history.push('/error'))
    }, [history, id, uid, setValues])

    const handleSubmit = async e => {
        e.preventDefault()
        setSubmitting(true)
        if(title.trim().length < 5) {
            setSubmitting(false)
            return Swal.fire('Error', 'Ingrese un título válido', 'error')
        }
        if(author.trim().length < 5) {
            setSubmitting(false)
            return Swal.fire('Error', 'Ingrese un autor válido', 'error')
        }
        if(publisher.trim().length < 3) {
            setSubmitting(false)
            return Swal.fire('Error', 'Ingrese una editorial válida', 'error')
        }
        if(!category?.value) {
            setSubmitting(false)
            return Swal.fire('Error', 'Ingrese una categoría válida', 'error')
        }
        if(isNaN(Number(price)) || Number(price) === 0) {
            return Swal.fire('Error', 'Ingrese un precio válido', 'error')
        }
        const book = {
            ...form,
            price: Number(price),
            category: category.value,
            isUsed,
        }
        if(file) {
            const newImage =  await uploadFile(file)
            book.imageUrl = newImage
        } else {
            book.imageUrl = imageUrl
        }
        await fetchWithToken(`books/${ id }`, book, 'PUT')
        setSubmitting(false)
        history.push('/')
    }

    const handleFileChange = e => {
        setFile(e.target.files[0])
    }

    const handleImgClick = () => {
        document.querySelector('#file').click()
    }

    return (
        <>
            <main className="sell">
                <section>
                    <div className="sell__container-left">
                    <FaTag className="sell__icon"/>
                    <p>Editar producto</p>
                    <img className="sell_pila-libros" src={ booksImg } alt="" />
                    </div>
                    <div className="sell__hero">
                    <div className="sell__trian"></div>
                    <div className="sell__container-form">
                        <h2><FaInfoCircle/> Carga la info</h2>
                        <form onSubmit={ handleSubmit }>
                        <div className="sell__form-inputs">
                            <div>
                            <article>
                                <label htmlFor="title">Titulo</label>
                                <input 
                                type="text" 
                                name="title"
                                value={ title }
                                onChange={ handleInputChange }
                                />
                            </article>
                            <article>
                                <label htmlFor="author">Autor</label>
                                <input 
                                type="text" 
                                name="author"
                                value={ author }
                                onChange={ handleInputChange }
                                />
                            </article>
                            </div>

                            <div>
                            <article>
                                <label htmlFor="publisher">Editorial</label>
                                <input
                                type="text"
                                name="publisher"
                                value={ publisher }
                                onChange={ handleInputChange }
                                />
                            </article>
                            <article>
                                <label>Categoria</label>
                                <Select 
                                className="sell__select" 
                                options={ categorySelectOptions } 
                                value={ category }
                                onChange={ setCategory }
                                />
                            </article>
                            </div>
                            <div>
                            <article>
                                <label htmlFor="price">Precio ARS</label>
                                <input 
                                type="text" 
                                name="price"
                                value={ price }
                                onChange={ handleInputChange }
                                />
                            </article>
                            <article className="sell__opt-estado">
                                <label htmlFor="state">Estado</label>
                                <div className="sell__cont-estados">
                                <div>
                                    <input
                                    type="radio"
                                    name="state"
                                    checked={ !isUsed }
                                    onChange={ () => setIsUsed(false) }
                                    />
                                    <label htmlFor="state">Nuevo</label>
                                </div>
                                <div>
                                    <input
                                    type="radio"
                                    name="state"
                                    checked={ isUsed }
                                    onChange= { () => setIsUsed(true) }
                                    />
                                    <label htmlFor="state">Usado</label>
                                </div>
                                </div>
                            </article>
                            </div>
                            <div>
                            <article>
                                <label htmlFor="imgUrl">Foto</label>
                                <input 
                                type="file"
                                style={{ display: 'none' }}
                                id="file"
                                onChange={ handleFileChange }
                                />
                                <p 
                                className="btn"
                                onClick={ handleImgClick }
                                >
                                {
                                    file ? file.name : 'Editar Foto'
                                }
                                </p>
                            </article>
                            </div>
                        </div>
                        <div className="sell__submit">
                            <button type="submit" disabled={ submitting }>Editar</button>
                        </div>
                        </form>
                    </div>
                    </div>
                </section>
                </main>
        </>
    )
}

export default EditScreen