const uploadFile = async file => {
    const cloudUrl = 'https://api.cloudinary.com/v1_1/drj6ew5ms/upload'

    if(!file) {
        return 'https://external-content.duckduckgo.com/iu/?u=http%3A%2F%2Fwww.cartoonbucket.com%2Fwp-content%2Fuploads%2F2017%2F03%2FAang-Holding-Stick-Image-600x600.jpg&f=1&nofb=1'
    }

    const formData = new FormData()
    formData.append('upload_preset', 'mercado_libros')
    formData.append('file', file)
    try {
        const response = await fetch( cloudUrl, {
            method: 'POST',
            body: formData
        })
        if(response.ok) {
            const cloudResp = await response.json()
            return cloudResp.secure_url
        } else {
            throw await response.json()
        }
    } catch (error) {
        throw error
    }
}

export default uploadFile