import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import Swal from 'sweetalert2'
import CategoryBook from '../components/CategoryBook'
import { fetchWithoutToken } from '../helpers/fetch'

const SearchScreen = () => {

    const { term } = useParams()

    const [books, setBooks] = useState(null)

    useEffect(() => {
        fetchWithoutToken(`books/search/${ term }`)
        .then(response => response.json())
        .then(body => setBooks(body.books))
        .catch(_ => Swal.fire('Error', 'Ocurrió un error', 'error'))
    }, [ term ])

    return (
        <div className="category__category">
            <section>
                <h2>Resultdos para "{ term }"</h2>
                <div className="category__books">
                    {
                        books && books.length
                        ? books.map(book => (
                            <CategoryBook key={ book.id } { ...book }/>
                        ))
                        : <h1>No se encontró ningún resultado</h1>
                    }
                </div>
            </section>
        </div>
    )
}

export default SearchScreen
