import React, { useState } from 'react'
import { FaInfoCircle, FaKey } from 'react-icons/fa'
import { useDispatch } from 'react-redux'
import { Link } from 'react-router-dom'
import Swal from 'sweetalert2'
import useForm from '../../hooks/useForm'
import { startRegister } from '../../store/actions/auth'

const RegisterScreen = () => {

  const dispatch = useDispatch()

  const [file, setFile] = useState(null)
  const [terms, setTerms] = useState(false)
  const [submitting, setSubmitting] = useState(false)

  const [ form, handleInputChange ] = useForm({
    firstName: '',
    lastName: '',
    email: '',
    dni: '',
    province: '',
    password: '',
    password2: ''
  })
  const { firstName, lastName, email, dni, province, password, password2 } = form

  const handleSubmit = e => {
    e.preventDefault()
    setSubmitting(true)
    if(!terms) {
      setSubmitting(false)
      return Swal.fire('Error', 'Por favor acepte los terminos y condiciones', 'error')
    }
    if(password !== password2) {
      setSubmitting(false)
      return Swal.fire('Error', 'Las contraseñas deben ser iguales', 'error')
    }
    dispatch(startRegister({
      firstName,
      lastName,
      email,
      dni,
      province,
      password
      },
      file
    ))
    setSubmitting(false)
  }

  const handleImgClick= () => {
    document.querySelector('#file').click()
  }

  const handleFileChange = e => {
    setFile(e.target.files[0])
  }

  const handleCheckboxChange = () => {
    setTerms(!terms)
  }

  return (
    <main className="login__screen login__register">
      <section className="login__container">
        <div className="login__signup">
          <FaKey fontSize="40"/>
          <h3>¿Ya tenes una cuenta?</h3>
          <Link to="/login"  className="login__registerbtn">Inicia Sesion</Link>
        </div>
        <div className="login__hero">
          <div className="login__trian"></div>
          <div className="login__signin">
            <h2><FaInfoCircle fontSize="40" /> Crea tu cuenta</h2>
            <form onSubmit={ handleSubmit }>
              <div className="login__form-inputs register__form-inputs">
                <div>
                  <article>
                    <label htmlFor="firstName">Nombre</label>
                    <input
                      type="text"
                      name="firstName"
                      value={ firstName }
                      onChange={ handleInputChange }
                    />
                  </article>
                  <article>
                    <label htmlFor="lastName">Apellido</label>
                    <input
                      type="text"
                      name="lastName"
                      value={ lastName }
                      onChange={ handleInputChange }
                    />
                  </article>
                </div>

                <div>
                  <article>
                    <label htmlFor="email">Email</label>
                    <input
                      type="email"
                      name="email"
                      value={ email }
                      onChange={ handleInputChange }
                    />
                  </article>
                  <article>
                    <label htmlFor="dni">DNI</label>
                    <input
                      type="text"
                      name="dni"
                      value={ dni }
                      onChange={ handleInputChange }
                    />
                  </article>
                </div>

                <div>
                  <article>
                    <label htmlFor="imgUrl">Foto de Perfil</label>
                    <input 
                    type="file"
                    style={{ display: 'none' }}
                    id="file"
                    onChange={ handleFileChange }
                    />
                    <p 
                    className="btn"
                    onClick={ handleImgClick }
                    >
                      {
                        file ? file.name : 'Subí un archivo'
                      }
                    </p>
                  </article>
                  <article>
                    <label htmlFor="province">Provincia</label>
                    <input
                      type="text"
                      name="province"
                      value={ province }
                      onChange={ handleInputChange }
                    />
                  </article>
                </div>

                <div>
                  <article>
                    <label htmlFor="password">Contraseña</label>
                    <input
                      type="password"
                      name="password"
                      value={ password }
                      onChange={ handleInputChange }
                    />
                  </article>
                  <article>
                    <label htmlFor="password2">Repetir Contraseña</label>
                    <input
                      type="password"
                      name="password2"
                      value={ password2 }
                      onChange={ handleInputChange }
                    />
                  </article>
                </div>
              </div>

              <div className="sell__cont-terminos">
                  <label className="sell__terminos"
                  >Acepto los terminos y condiciones
                  <input 
                  type="checkbox"
                  checked={ terms }
                  onChange={ handleCheckboxChange }
                  />
                  <span className="sell__checkmark"></span>
                  </label>
              </div>
              <div className="submit">
                <button type="submit" disabled={ submitting }>Crear Cuenta</button>
              </div>
            </form>
          </div>
        </div>
      </section>
    </main>
  )
}

export default RegisterScreen
