import Swal from "sweetalert2"
import { fetchWithoutToken, fetchWithToken } from "../../helpers/fetch"
import types from "../types"

export const startGetCart = () => {
    return async dispatch => {
        try {
            const response = await fetchWithToken('cart', null)
            const { cart } = await response.json()
            dispatch(getCart(cart))
        } catch (error) {
            Swal.fire('Error', 'Ocurrió un error', 'error')
        }
    }
}

const getCart = (cart) => ({
    type: types.CART_GET,
    payload: cart
})

export const startAddToCart = id => {
    return async dispatch => {
        try {
            const cartResponse = await fetchWithToken(`cart/add/${ id }`, { id }, 'POST')
            const body = await cartResponse.json()
            const bookResponse = await fetchWithoutToken(`books/${ id }`, null)
            const {book} = await bookResponse.json()

            if(body.ok) {
                Swal.fire('Éxito', 'El libro fue agregado al carrito', 'success')
                dispatch(addToCart(book))
            }
            else {
                Swal.fire('Error', body.msg, 'error')
            }
        } catch (error) {
            Swal.fire('Error', 'Ocurrió un error', 'error')
        }
    }
}

const addToCart = book => ({
    type: types.CART_ADD,
    payload: book
})

export const startDeleteFromCart = id => {
    return async dispatch => {
        try {
            await fetchWithToken(`cart/delete/${ id }`, { id }, 'DELETE')

            dispatch(deleteFromCart(id))
        } catch (error) {
            Swal.fire('Error', 'Ocurrió un error', 'error')
        }
    }
}

const deleteFromCart = id => ({
    type: types.CART_REMOVE,
    payload: id
})